/* Variables */
$fuentePrincipal: 'Josefin Sans', sans-serif;
$fuenteSecundario: 'Roboto', sans-serif;
$colorPrincipal: #FFC8EA;
$colorSecundario: #FFF18F;
$colorTerceario: #FFE7D7;

/* pagina resultados */
body{
    font-family: $fuentePrincipal;
}
/*Reseteo*/
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

header{
    display: flex;
    justify-content: center;
    justify-content: center;
    flex-direction: row;
        nav{
            margin: 1vw 0;
            background-color: $colorPrincipal;
            width: 85%;
            height: 3.2vw;
            border-radius: 25px;
            display: flex;
            justify-content: space-between;
            align-items: center;
            .logo{
                display: flex;
                img{
                    width: 5vw;
                    height: 5vw;
                }
                h2{
                    font-size: 3vw;
                    display: flex;
                    align-items: center;
                }
            }
        .enlaceTienda{
            margin-left: 70%;
            a{
                font-size: 2vw;
                font-family: $fuentePrincipal;
                text-decoration: none;
            }
        }

        }
        .Carrito{
            width: 4vw;
            height: 4vw;

            margin-top: 0.5vw;
    }
}
.Portada {
    display: flex;
    flex-direction: column;
    h1{
        font-family: $fuentePrincipal;
        text-align: center;
        font-size: 3vw;
    }
    img{
        width: 90%;
        margin: 0 auto;
    }
}
.Tienda{
    .subtitulo{
        margin: 1vw 0;
        background-color: $colorSecundario;
        width: 85%;
        height: 3.2vw;
        border-radius: 25px;
        display: flex;
        margin: 0 auto;
        display: flex;
        justify-content: center;
        align-items: center;
        font-size: 1.5vw;
        margin-top: 1vw;
    }
    p{
        display: flex;
        margin: 0 25% 0 25%;
        flex-direction: column;
        font-family: $fuenteSecundario;
        font-size: 1vw;
        span{
            font-weight: bold;
            margin-top: 1vw;
        }
    }
}  

#productos{
        .categoria{
            display: flex;
            flex-direction: column;
            h3{
                text-align: center;
                margin-top: 2vw;
                font-size: 4vw;
                font-family: $fuentePrincipal;
            }
            .productosCategoria{
                display: grid;
                grid-template-columns: repeat(3, 1fr);
                margin: 0 auto;
                .producto{
                    border-radius: 1vw;
                    border: gray  0.1vw solid;
                    width: 12vw;
                    height: 16vw;
                    display: flex;
                    flex-direction: column;
                    align-items: center;
                    margin: 2vw;
                    img{
                        height: 8vw;
                        width: auto;
                        margin: 1vw;
                    }
                    .botonComprar{
                        border-radius: 1vw;
                        background-color: $colorPrincipal;
                        width: 8vw;
                        h4{
                            color: black;
                            padding: 1vw;
                            text-align: center;
                            font-family: $fuenteSecundario;
                            font-size: 1vw;
                        }
                    }
                }
            }
        }
}